<template>
  <div>正在热映
    <ul>
      <li v-for="(data,filmId) in datalist"
          :key="filmId"
          @click="handleChangePage(data)">{{data.name}}
      </li>
    </ul>
  </div>

</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      datalist: []
    };
  },
  mounted () {
    // axios.get('https://m.maizuo.com/gateway?cityId=440100&pageNum=1&pageSize=10&type=1&k=8866764').then(res => {
    //   console.log(red.data)
    // })
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=440100&pageNum=1&pageSize=10&type=1&k=8866764',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16073211741346815844679681"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      console.log(res.data)
      this.datalist = res.data.data.films
    })

  },

  components: {},

  computed: {},

  methods: {
    handleChangePage (name) {
      //两种跳转方法
      // this.$router.push(`/detail/${name}`)
      this.$router.push({ name: "detailpage", params: { id: name } })
    }
  }
}
</script>
<style lang='css' scoped>
</style>


