<template>
  <nav>
    <ul>

      <router-link to="/film"
                   tag="li"
                   activeClass="highlight">flme</router-link>

      <router-link to="/cinema"
                   tag="li"
                   active-class="highlight">cinema</router-link>

      <router-link to="/center"
                   tag="li"
                   active-class="highlight">center</router-link>

    </ul>
  </nav>
</template>

<script>
export default {
  data () {
    return {

    };
  },

  components: {},

  computed: {},

  methods: {}
}
</script>
<style lang='scss' scoped>
.highlight {
  color: red;
}

nav {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: white;
  ul {
    display: flex;
    li {
      flex: 1;
      line-height: 50px;
      text-align: center;
    }
  }
}
</style>


